@keyframes sec-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__arrow-min {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 20vmin;
  animation: 3600s steps(60, end) infinite sec-move;
  position: absolute;
}

.stopwatch__arrow-sec {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-bottom: 38vmin;
  animation-name: sec-move;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: fixed;
}

.stopwatch--speed-up .stopwatch__arrow-min {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__arrow-sec {
  animation-duration: 10s;
}
